*{
    margin:0;
    padding:0;
    box-sizing:border-box;
  }
  
  
  .outer{
    display:flex;
    align-items:center;
    justify-content:center;
    flex-direction:column;
    max-width:100vw;
    height:100vh;
    background-color:gold;
  
  }
  .front-box{
    display:flex;
    flex-direction:column;
    padding :auto;
    justify-content:space-between;
    align-items:center;
    margin:auto;
    max-width:250px;
    height:150px;
    margin-top:20px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    background-color:red;
  
  }
  .input{
    outline:none;
    cursor:text;
    height:30px;
    border-top:none;
    border-left:none;
    border-right:none;
    border-bottom:2px solid black
  
  
  
  
  }
  
  .btn{
    height: 30px;
    background-color: yellow;
    border: none;
    outline: none;
    max-width: -moz-fit-content;
    min-width: fit-content;
    width: 60px;
    position: relative;
  
    border-color: white;
    border-radius: 9px;
  }
  
  .after{
  
  height:450px;
  width:650px;
  margin-top:20px;
  
  overflow-y:auto;
  overflow-x:hidden;
  background-color:black;
  
  }
  .message-box{
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
    margin-top:0px;
    width:650px;
  
  
    height:30px;
  
  }
  .input-2{
    outline:none;
    height:100%;
    width:100%;
    border-color:white;
    border-radius:8px;
    text-align:center;
  
    background-color:blue;
  
  }
  .input-2::placeholder{
    font-weight:bold;
    color:black;
  }
  .message{
    display:flex;
    background-color:white;
    color:black;
    font-size:10px;
    border-radius:5px;
    width:fit-content;
    height:30px;
    text-align:center;
    align-items:center;
    justify-content:center;
    margin-top:8px;
  }
  .left{
    float:left;
    clear:both;
    margin-left:4px;
  }
  .right{
    float:right;
    clear:both;
    margin-right:4px;
  }